*{
  margin: 0;
  font-family: 'Roboto', sans-serif;
  overflow-x: hidden;
  
}

:root{
  --main-color: #0b0b0b;
}

.home{
  background-color: var(--main-color);
  
  
  
}

.navbar{
  width: 100%;
  color: #ffffff;
  font-size: 14px;
  position: fixed;
  top: 0;
  z-index: 9999;
  background: linear-gradient(to top, transparent 0%, rgb(0,0,0,0.3) 50%);

  &.scrolled{
    background-color: var(--main-color);
  }
  .container{
    padding: 0 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 70px;

    .left{
      display: flex;
      align-items: center;
    img{
      height: 55px;
      margin-right: 40px;
    }
  }

  span{
    margin-right: 20px;
    cursor: pointer;
  }

  .right{
    display: flex;
    align-items: center;

    input{
      display: none;
    }

    &:hover{
      input{
        display: block;
        padding: 10px;
        background-color: #ffffff;
        width: 200px;
        height: 10px;
        border: none;
        margin: 0 10px;
        
      }
    }

  .icon{
    margin: 0px 15px;
    cursor: pointer;
    size: 20;
  }
    img{
      width: 30px;
      height: 30px;
      border-radius: 5px;
      object-fit: cover;
    }

    .profile{
      .options{
        display: none;
        background-color: var(--main-color);
      }

      span{
        padding: 10px;
        cursor: pointer;
      }

      &:hover{
        .options{
          display:flex;
          flex-direction: column;
          position: absolute;
        }
      }
    }
  }
  }
}

.featured{
  position: relative;
  height: 120vh;
  overflow-y: hidden;

  img{
    position: relative;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
  }

  .category{
      position: absolute;
      top: 80px;
      left: 50px;
      font-size: 30px;
      font-weight: 500;
      color: white;
      display: flex;
      align-items: center;
      z-index: 9;    
      
      

      select{
        cursor: pointer;
        box-sizing: content-box;
        background-color: var(--main-color);
        border: 1px solid white;
        color: white;
        margin-left: 20px;
        height: 30px;

        option{
          font-size: 16px;
          
        }
      }
  }

  .info{
      img{
      position: absolute;
      top: 120px;
      left: 60px;
      width: 70%;
      height: 65%;
    }
    .desc{
      width: 35%;
      position: absolute;
      color: white;
      left: 10%;
      bottom: 25%;
      margin: 3% 0%;
      text-shadow: 2px 2px 8px black;
    }
    .buttons{
      display: flex;
      width: 35%;
      position: absolute;
      color: white;
      left: 8%;
      bottom: 20%;
      margin: 2% 0%;
      border-radius: 20px;
      padding: 16px;
      
    }
    button{
      padding: 10px;
      gap: 1rem;
      border-radius: 5px;
      border: none;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 15px;
      width: 20%;
      margin: -2% 1%;
      top: 10%;
      cursor: pointer;
      color: white;
      &.play{
        background-color: #ffffff;
        color: black;
        box-shadow: 2px 2px 8px black;
      }
      &.info{
        background-color: grey;
        box-shadow: 2px 2px 8px black;
      }
    } 
  }
}

.List{
  color: white;
  width: 100%;
  margin-top: 10px;
  

  .listTitle{
    color: #ffffff;
    font-size: 20px;
    font-weight: 500;
    padding-bottom: 20px;
    margin-left: 70px;
  }

  .wrapper{
    position: relative;
    .leftarrow{
      width: 50px;
      height: 100%;
      background-color: rgb(22,22,22,0.5);
      position: absolute;
      left: 0;
      z-index: 99;
      top: 0;
      bottom: 0;
      margin: auto;
      cursor: pointer;

      &:hover{
        background-color: gainsboro;
      }
    }
  
    .rightarrow{
      width: 50px;
      height: 100%;
      background-color: rgb(22,22,22,0.5);
      position: absolute;
      right: 0;
      z-index: 99;
      top: 0;
      bottom: 0;
      margin: auto;
      cursor: pointer;
      &:hover{
        background-color: gainsboro;
      }
    }

    .container{
      margin-left: 50px;
      display: flex;
      margin-top: 10px;
      width: max-content;
      transform: translateX(0px);
      transition: all 1s ease;
    }
  }

  .Listitem{
    width: 350px;
    height: 170px;
    background-color: var(--main-color);
    padding-bottom:20px;
     color: white;
     overflow: hidden;
    margin-left: 20px;

    img{
      width: 100%;
      height: 98%;
      object-fit: fill;
    }

    video{
      width: 200px;
      height: 100px;
      display: none;
    }

    &:hover{
      width: 500px;
      height: 400px;
      top: -150px;
      -webkit-box-shadow: 0px 2px 15px 0px rgba(255,255,255,0.7);;
      box-shadow: 0px 2px 15px 0px rgba(255,255,255,0.7);
      color: white;

      img{
        display: none;
      }
      iframe{
        display: block;
        width: 100%;
        height: 50%;

      }
    }
    .itemInfo{
      padding-left: 10px;

      .icons{
        display: flex;
        gap: 7px;
        padding-bottom:10px;
        padding-top: 10px;

        .icon{
          border: 2px solid white;
        padding: 5px;
        border-radius: 50%;
        }
      }
    }

    .itemInfoTop{
      display: flex;
      align-items: center;
      margin-bottom: 10px;
      font-size: 14px;
      font-weight: 600;
      color: gray;

      .limit{
        border: 2px solid white;
        margin: 0 10px;
        padding: 3px;
      }
    }
  }

}

footer{
  background-color: #0b0b0b;
  color: white;
  font-weight: 500;
  text-align: center;
  font-size: 20px;
  padding: 30px;
  margin-bottom: 20px;

  span{
    padding-left: 100px;
    padding-right: 100px;
    padding-top: 50px;
    padding-bottom: 50px;
    cursor: pointer;
  }

}

.webicon{
  border: 2px solid white;
  margin-left: 45px;
  width: 15%;
  padding-left: 20px;
  display: flex;
  flex-direction: row;
  align-items: center;

  select{
    margin: 20px;
    display: flex;
    flex-direction: column;
    background-color: #0b0b0b;
    color: #ffffff;
    border: none;
  }
}

.brand{
  margin-left: 60px;
  margin-top: 10px;
  cursor: pointer;
}